:root {
  /* колір тексту у хедері, світла тема */
  --color-text-header-l: #595959;
  /* колір тексту у хедері, темна тема */
  --color-text-header-d: #B7B7B7;
  /* колір тексту у описах */
  --color-text-description: #F8F8F8;
  /* колір тексту у кнопках та хедер темної темі */
  --color-black: #111111;
  /* колір боді на темній темі та хрестик на модалці */
  --color-second-black: #282828;
  /* колір тексту у заголовках та боді на світлій темі */
  --color-white: #FFFFFF;

  --color-text-button: #F87719;
  --color-button: linear-gradient(
                  141.22deg, 
                  #FFC226 9.4%, 
                  #F84119 91.91%);
  --color-button-hover: linear-gradient(
                      141.22deg, 
                      #F84119 9.4%, 
                      rgba(248, 159, 25, 0.68) 91.91%);

  --transition-default: 250ms cubic-bezier(0.4, 0, 0.2, 1);  
  --shadow: 1px 1px 14px rgba(1, 1, 1, 0.25);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Roboto', sans-serif;
  overflow-x: hidden;
}


*,
*::before,
*::after {
  box-sizing: inherit;
}
:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}
ul, li, a, p {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
button, link {
  cursor: pointer;
}

/* приховати код */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

